import React, { useState } from "react";

const ImageComponent = ({ listing }) => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [xVal, setXVal] = useState(0);
  const [transformXStyle, setTransformXStyle] = useState({
    transform: "translateX(0px)",
  });

  const renderImages = () => {
    return listing.photos.map((photo, index) => {
      let imageClass = "images";
      if (index === activeIndex) imageClass = "images active";
      return (
        <>
          <img src={photo.url} className={`${imageClass} item2`} />
        </>
      );
    });
  };

  const imageNavigation = (val) => {
    const newVal = activeIndex + val;
    if (newVal >= 0 && newVal < listing.photos.length) {
      setActiveIndex(newVal);
    }
  };
  const navBarSlide = (indexInput) => {
    if (indexInput - 2 > 0) {
      setXVal((indexInput - 2) * -20);
    } else {
      setXVal(0);
    }
    setTransformXStyle({ transform: `translateX(${xVal}px)` });
  };

  const renderNavButtons = () => {
    return listing.photos.map((photo, index) => {
      let buttonClass = "buttons";
      if (index === activeIndex) buttonClass = "buttons active";

      return (
        <li>
          <button
            className={buttonClass}
            onClick={() => {
              imageNavigation(index - activeIndex);
              navBarSlide(index);
            }}
          ></button>
        </li>
      );
    });
  };

  return (
    <>
      <a
        href="#"
        onClick={(e) => {
          e.preventDefault();
          imageNavigation(-1);
          navBarSlide(activeIndex);
        }}
        className="item1"
      >
        {"<"}
      </a>
      {renderImages()}
      <div className="item4">
        <ul className="imageButtons" style={transformXStyle}>
          {renderNavButtons()}
        </ul>
      </div>
      <a
        href="#"
        onClick={(e) => {
          e.preventDefault();
          imageNavigation(1);
          navBarSlide(activeIndex);
        }}
        className="item3"
      >
        {">"}
      </a>
    </>
  );
};

export default ImageComponent;
