import {
  UPDATE_LISTINGS,
  DECREMENT,
  INCREMENT,
  TRIGGER_RENDER,
} from "../actions/listings";

export default function listings(state = [], action) {
  const oldStartIndex =
    typeof state.startIndex === "undefined" ? 0 : state.startIndex;
  const oldEndIndex =
    typeof state.endIndex === "undefined" ? 3 : state.endIndex;

  switch (action.type) {
    case UPDATE_LISTINGS:
      const oldListings =
        typeof state.listings === "undefined" ? [] : state.listings;
      if (state.firstRender) {
        return {
          ...state,
          listings: oldListings.concat(action.listings),
          firstRender: false,
          startIndex: oldStartIndex,
          endIndex: oldEndIndex,
        };
      }
      return {
        ...state,
        listings: action.listings,
        firstRender: false,
        startIndex: oldStartIndex,
        endIndex: oldEndIndex,
      };

    case DECREMENT:
      // let newDecrementStartIndex = oldStartIndex - 4;
      // let newDecrementEndIndex = oldEndIndex - 4;
      // if (newDecrementStartIndex < 0) {
      //   newDecrementStartIndex = state.listings.length - 3;
      //   newDecrementEndIndex = state.listings.length;
      // }
      if (oldStartIndex > 0) {
        return {
          ...state,
          startIndex: oldStartIndex - 4,
          endIndex: oldEndIndex - 4,
        };
      }
      return {
        ...state,
      };

    case INCREMENT:
      // let newIncrementStartIndex = oldStartIndex + 4;
      // let newIncrementEndIndex = oldEndIndex + 4;
      // if (newIncrementStartIndex > state.listings.length) {
      //   newIncrementStartIndex = 0;
      //   newIncrementEndIndex = 3;
      // }
      return {
        ...state,
        startIndex: oldStartIndex + 4,
        endIndex: oldEndIndex + 4,
      };
    case TRIGGER_RENDER:
      return {
        ...state,
        firstRender: true,
      };
    default:
      return state;
  }
}
