import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import ListingItem from "./ListingItem";
import { fetchData, fetchDataIncrement } from "../actions/listings";

function mapStateToProps(state) {
  return {
    listings: state.listings.listings,
    firstRender: state.listings.firstRender,
    startIndex: state.listings.startIndex,
    endIndex: state.listings.endIndex,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    fetchData: () => {
      dispatch(fetchData);
    },
    fetchDataIncrement: () => {
      dispatch(fetchDataIncrement);
    },
    increment: () => {
      dispatch({ type: "INCREMENT" });
    },
    decrement: () => {
      dispatch({ type: "DECREMENT" });
    },
    triggerRender: () => {
      dispatch({ type: "TRIGGER_RENDER" });
    },
  };
}

class ListingCarousel extends Component {
  static propTypes = {
    listings: PropTypes.object,
    firstRender: PropTypes.bool,
    startIndex: PropTypes.number,
    endIndex: PropTypes.number,
  };

  constructor(props) {
    super();
    this.state = {
      startIndex: props.startIndex,
      endIndex: props.endIndex,
      listings: props.listings,
      isTest: [],
      firstRender:
        typeof props.firstRender !== "undefined" ? props.firstRender : true,
    };
    this.updateListing = this.updateListing.bind(this);
  }

  componentWillUpdate(nextProps) {
    console.log("nextProps is", nextProps);
    if (nextProps.startIndex !== this.props.startIndex) {
      this.setState((state) => {
        return {
          ...this.state,
          listings: nextProps.listings,
          firstRender: nextProps.firstRender,
          startIndex: nextProps.startIndex,
          endIndex: nextProps.endIndex,
        };
      });
    }
    // if (nextProps.listings !== this.props.listings) {
    //   this.setState((state) => {
    //     return {
    //       ...this.state,
    //       listings: nextProps.listings,
    //       firstRender: nextProps.firstRender,
    //       startIndex: nextProps.startIndex,
    //       endIndex: nextProps.endIndex,
    //     };
    //   });
    // }
  }

  updateListing(id) {
    this.setState({
      ...this.state,
      listings: this.state.listings.map((listing) => {
        const isTest = listing.id === id;
        if (!isTest) return listing;
        return {
          ...listing,
          isTest,
        };
      }),
    });
  }

  render() {
    const { listings, startIndex, endIndex, firstRender } = this.state;

    console.log(this.state.firstRender);
    if (this.state.firstRender) {
      this.props.fetchData();
    }

    if (!listings) return null;
    console.log("LISTINGS", listings);

    const iterateListings = () => {
      return listings.map((listing, index) => {
        if (index >= startIndex && index <= endIndex) {
          return (
            <ListingItem
              cssId="listItem"
              key={index}
              listing={listing}
              onClick={this.updateListing}
            />
          );
        }
      });
    };

    const assessIncrement = () => {
      if (endIndex >= listings.length - 3) {
        return this.props.fetchDataIncrement;
      }

      return this.props.increment;
    };

    return (
      <>
        <h1 id="mainTitle">Listings with videos</h1>
        <a href="#" id="leftArrow" onClick={this.props.decrement}>
          {"<"}
        </a>
        {iterateListings()}
        <a href="#" id="rightArrow" onClick={assessIncrement()}>
          {">"}
        </a>
      </>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ListingCarousel);
